.recipe_card{
    width: 98%;
    height: 35vh;
    background-color: #faebd7;    
    background: linear-gradient(45deg, #e9d8c3ed 0%, #dfd9cd 44%, #e9d8c3ed 100%);
    padding: .5em 0em;
    text-decoration: none;
    text-shadow: 1px 1px 1px #41381c;
    overflow: scroll;
    border-radius: 15px;
    box-shadow: 4px 4px 2px #41381cc4;
    transition: -webkit-transform 0.5s;
    transition: transform 0.5s;
    transition: transform 0.5s, -webkit-transform 0.5s;
}

.recipe_card:hover{ 
    width: 100%;   
    height: 35vh;
    background: linear-gradient(90deg, #faebd7ed 0%, #fffaee 44%, #faebd7ed 100%);
    transition: .5s;
}

.card_image{
    width: 90%;
    height: 60%;
    border-radius: 10px;
    object-fit: cover;
    object-position: center center;
    box-shadow: 2px 2px 2px #41381c;
}

.card_name{
    font-size: 1em;
    font-weight: 600;
    padding-left: 1em;
    padding-right: 1em;
    text-decoration: none;
}

.card_diets{
    font-size: .8em;
    font-weight: 400;
    padding-left: 1em;
    padding-right: 1em;
}
.card_container{
    position: absolute;
    top: 22vh;
    left: 0;
    width: 100%;
    padding-top: 2.5vh;
    display: grid;
    grid-gap: 1em;
    grid-template-columns: repeat(auto-fit, minmax(13em, 13em));
    text-align: center;
    text-decoration: none;
    margin: 0em 1em;
    z-index: 3;
}

.back_image{
    position:absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 0;
}

.main_title{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 13vh;
    padding-bottom: 1vh;
    color: #41381c;
    font-family: 'Ms Madi', cursive;
    font-size: 3em;
    font-weight: 550;
    text-shadow: 1px 1px 1px #41381c;
    background-color: #faebd7bd;
    border-radius: 20px;
    transition: -webkit-transform 0.5s;
